/* global __KOOT_SSR__:false */

const { needConnectComponents } = require('../defaults/defines-server');

/**
 * 
 * - : 
 * - : SSR `dataToStore` 
 * @returns {boolean}
 */
module.exports = () => {
    if (__SERVER__) {
        const SSR = __DEV__ ? global.__KOOT_SSR__ : __KOOT_SSR__;
        if (SSR[needConnectComponents]) return false;
        return true;
    }
    return true;
};
