const injectHtmlLang = require('./inject/htmlLang');
const injectMetas = require('./inject/metas');
const injectStyles = require('./inject/styles');
const injectScripts = require('./inject/scripts');

/**
 *  ejs 
 * @param {Object} options 
 * @param {Object} [options.injectCache={}] /
 * @param {Object} [options.filemap={}] () 
 * @param {Object} [options.entrypoints={}] () 
 * @param {String} [localeId]  ID
 * @param {String} [title] 
 * @param {String} [metaHtml] meta  HTML 
 * @param {String} [reactHtml]  React  HTML 
 * @param {String} [stylesHtml]  HTML 
 * @param {String} [reduxHtml]  redux store  HTML 
 * @param {Object} [SSRState] SSR 
 * @param {Object} [needInjectCritical]  critical 
 * @param {Boolean} [needInjectCritical.styles=false]
 * @param {Boolean} [needInjectCritical.scripts=false]
 * @returns {Object}
 */
module.exports = (options = {}) => {
    const {
        injectCache = {},

        filemap = {},
        entrypoints = {},
        compilation,

        localeId,

        title,
        metaHtml,
        reactHtml,
        stylesHtml,
        reduxHtml,
        SSRState,

        needInjectCritical = {
            styles: false,
            scripts: false
        }
    } = options;

    return {
        htmlLang: injectHtmlLang(localeId),
        title,
        metas: injectMetas({ metaHtml, localeId, compilation }),
        styles: injectStyles({
            needInjectCritical: needInjectCritical.styles,
            injectCache,
            filemap,
            stylesHtml,
            localeId,
            compilation
        }),

        react: reactHtml,

        scripts: injectScripts({
            needInjectCritical: needInjectCritical.scripts,
            injectCache,
            entrypoints,
            localeId,
            reduxHtml,
            SSRState,
            compilation
        })
    };
};
