const itemsBlacklist = ['localeId', 'realtimeLocation', 'routing', 'server'];

/**
 *  state state
 *
 * 
 * - `localeId`
 * - `realtimeLocation`
 * - `routing`
 * - `server`
 *
 * @param {Object} state
 * @returns {Object}
 */
const filter = oldState =>
    itemsBlacklist.reduce((state, item) => {
        const { [item]: _, ...rest } = state;
        return rest;
    }, oldState);

module.exports = filter;
