import resetStore from '../../../../React/redux/reset-store';

/**
 *  redux  redux store 
 *
 * -  store  (`factoryStore`):  store
 * -  store (`store`):  state  reducer 
 *
 *  redux store
 *
 * @param {Object} reduxConfig Redux
 * @param {Function} [reduxConfig.factoryStore]  Redux Store 
 * @param {Object} [reduxConfig.store] Redux Store 
 * @returns {Object} store
 */
const initStore = (reduxConfig = {}) => {
    const { store, factoryStore } = reduxConfig;

    if (typeof factoryStore === 'function') {
        return factoryStore();
    }

    if (typeof store === 'object') {
        return resetStore(store);
    }

    return {};
};

export default initStore;
